import { useLocation, useNavigate } from 'react-router-dom';
import 'react-datepicker/dist/react-datepicker.css';
import { useState, useEffect, useRef } from 'react';
import 'assets/css/batteriesAddOrUpdate.css';
import { useDispatch, useSelector } from 'react-redux';
import { showMessage } from 'app/store/fuse/messageSlice';
import { DetailTitleBar } from 'app/theme-layouts/mainLayout/components';
import { open, close } from 'app/store/common/loadingWrap';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { selectComboBox } from 'app/store/common/comboBox';
import { selectProject, setProject } from 'app/store/common/project';

function AddOrUpdate() {
  const dispatch = useDispatch();
  const companyComboBox = useSelector(selectComboBox).company;
  const [companyCategory, setCompanyCategory] = useState([]);
  const [companyId, setCompanyId] = useState('');
  const navigate = useNavigate();
  const location = useLocation();
  const param = location.state;
  const nameInput = useRef();
  const projectDesc = useRef();
  const useYn = useRef();
  const [validation, setValidation] = useState([]);
  const dataList = useSelector(selectProject).project;

  const saveBtnClick = () => {
    confirmAlert({
      title: `프로젝트를 ${param.detailLink[0].title} 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            dispatch(open());
            const projectObj = {};
            projectObj.companyId = companyId;
            projectObj.projectName = nameInput.current.value;
            projectObj.projectDesc = projectDesc.current.value;
            projectObj.useYn = useYn.current.value;

            const filter1 = dataList.filter((obj) => obj.projectName === nameInput.current.value);
            if (filter1.length > 0) {
              dispatch(showMessage({ message: '프로젝트 정보 저장 실패.', variant: 'error' }));
              setValidation([{ type: 'projectName', message: '이미 존재하는 프로젝트명입니다.' }]);
              dispatch(close());
              return;
            }
            const today = new Date();
            const year = today.getFullYear();
            const month = `0${today.getMonth() + 1}`.slice(-2);
            const day = `0${today.getDate()}`.slice(-2);
            const dateString = `${year}-${month}-${day}`;
            const hours = `0${today.getHours()}`.slice(-2);
            const minutes = `0${today.getMinutes()}`.slice(-2);
            const seconds = `0${today.getSeconds()}`.slice(-2);
            const timeString = `${hours}:${minutes}:${seconds}`;
            const clone = dataList.slice();
            const filter = clone.filter((obj) => obj.projectId !== param.projectId);
            switch (param.detailLink[0].title) {
              case '추가':
                clone.push({
                  projectId: Number(dataList[dataList.length - 1].projectId) + 1,
                  projectName: nameInput.current.value,
                  projectDesc: projectDesc.current.value,
                  companyId,
                  companyName: '(주)한빛',
                  groupCnt: 0,
                  deviceCnt: 0,
                  useYn: useYn.current.value,
                  updateUsername: '한빛 관리자',
                  updateTime: `${dateString} ${timeString}`,
                });
                dispatch(close());
                dispatch(setProject({ data: clone }));
                dispatch(
                  showMessage({ message: '프로젝트 정보가 추가되었습니다.', variant: 'success' })
                );
                navigate('/bss_server/systemManagement/project');
                break;
              case '수정':
                filter.push({
                  projectId: param.projectId,
                  projectName: nameInput.current.value,
                  projectDesc: projectDesc.current.value,
                  companyId,
                  companyName: '(주)한빛',
                  groupCnt: 0,
                  deviceCnt: 0,
                  useYn: useYn.current.value,
                  updateUsername: '한빛 관리자',
                  updateTime: `${dateString} ${timeString}`,
                });
                dispatch(close());
                dispatch(setProject({ data: filter }));
                dispatch(
                  showMessage({ message: '프로젝트 정보가 수정되었습니다.', variant: 'success' })
                );
                navigate('/bss_server/systemManagement/project');
                break;
              default:
            }
          },
        },
        {
          label: '취소',
        },
      ],
    });
  };

  const deleteBtnClick = () => {
    confirmAlert({
      title: `프로젝트 정보를 삭제 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            dispatch(open());
            const clone = dataList.slice();
            const filter = clone.filter((obj) => obj.projectId !== param.projectId);
            dispatch(close());
            dispatch(setProject({ data: filter }));
            dispatch(
              showMessage({ message: '프로젝트 정보가 삭제되었습니다.', variant: 'success' })
            );
            navigate('/bss_server/systemManagement/project');
          },
        },
        {
          label: '취소',
        },
      ],
    });
  };

  useEffect(() => {
    setCompanyCategory(companyComboBox);
    param.detailLink[0].title === '추가'
      ? setCompanyId(companyComboBox[0].code)
      : setCompanyId(param.companyId);
  }, [param.detailLink[0].title, param.companyId]);

  return (
    <div className="section__grid__wrap content project__manage batteries__add__update">
      <DetailTitleBar
        flag={param.detailLink[0].title}
        deleteBtnClick={deleteBtnClick}
        saveBtnClick={saveBtnClick}
      />
      {/* input__wrap */}
      <section className="input__wrap">
        <div className="section__inner">
          <ul className="selector__wrap">
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  프로젝트명
                </dt>
                <dd>
                  <input
                    ref={nameInput}
                    defaultValue={param.projectName}
                    type="text"
                    className="custom__input"
                  />
                  {validation.map(
                    (obj) =>
                      obj.type === 'projectName' && (
                        <p className="f__medium field__error">{obj.message}</p>
                      )
                  )}
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  회사명
                </dt>
                <dd>
                  <select
                    value={companyId}
                    className="custom__select"
                    onChange={(evt) => {
                      setCompanyId(evt.target.value);
                    }}
                  >
                    {companyCategory.map((obj, idx) => (
                      <option key={obj.code} value={obj.code}>
                        {obj.codeName}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
            </li>
            <li className="w100">
              <dl>
                <dt>설명</dt>
                <dd>
                  <textarea
                    ref={projectDesc}
                    defaultValue={param.projectDesc}
                    className="custom__input"
                  />
                </dd>
              </dl>
            </li>
            <li className="w50">
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  사용여부
                </dt>
                <dd>
                  <select className="custom__select" defaultValue={param.useYn} ref={useYn}>
                    <option value="1">사용</option>
                    <option value="0">미사용</option>
                  </select>
                </dd>
              </dl>
            </li>
          </ul>
        </div>
      </section>
      {/* // input__wrap */}
    </div>
  );
}

export default AddOrUpdate;
